import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  { path: '/', name: 'home', component: HomeView },
  { path: '/phase',  name: 'phase', component: () => import( '../views/ThePhase.vue')},
  { path: '/portfolo',  name: 'portfolo', component: () => import( '../views/ThePortfolio.vue')},
  { path: '/team',  name: 'team', component: () => import( '../views/TheTeam.vue')},
  { path: '/achievement',  name: 'achievement', component: () => import( '../views/TheAchievement.vue')},
  { path: '/contact',  name: 'contact', component: () => import( '../views/TheContact.vue')},
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
